@import url(https://fonts.googleapis.com/css2?family=Mooli&family=Nunito:wght@200;300;400;500;600;700;800;900;1000&display=swap);
.liste_container {
  padding: 0;
  margin-top: 32px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.liste_element {
  background-color: var(--couleur_tertiaire);
  list-style-type: none;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid var(--couleur_secondaire);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.poubelle {
  color: var(--couleur_tertiaire);
  background-color: rgb(253, 107, 107);
  padding: 7px;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
}

#liste_element {
  color: var(--couleur_secondaire);
  font-weight: 700;
}

.form_container {
  display: flex;
  flex-direction: column;
  grid-gap: 32px;
  gap: 32px;
  margin-top: 50px;
}

.tache {
  border: 2px solid var(--couleur_secondaire);
  border-radius: 5px;
  max-width: 77%;
  height: 24px;
}

.tache:focus {
  outline: none;
}

.tache::-webkit-input-placeholder {
  color: black;
  font-weight: 600;
}

.tache::placeholder {
  color: black;
  font-weight: 600;
}

.tache:focus::-webkit-input-placeholder {
  visibility: hidden;
}

.tache:focus::placeholder {
  visibility: hidden;
}

.urgent {
  border: 2px solid var(--couleur_secondaire);
  background-color: white;
  border-radius: 5px;
  max-width: 75%;
  height: 24px;
  padding: 3px 0 3px 10px;
  display: flex;
  grid-gap: 11px;
  gap: 11px;
  margin-bottom: 25px;
}

#urgent {
  text-decoration: underline;
}

.urgent_checkbox {
  margin: 0;
  cursor: pointer;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  border-radius: 4px;
  height: 15px;
  width: 15px;
  background: #fff;
  border: 1px solid #969696;
  margin-top: 4px;
}

input[type="checkbox"]:checked {
  background: var(--couleur_secondaire);
  position: relative;
}

.submit_btn {
  position: relative;
  border: none;
  background-color: var(--couleur_secondaire);
  cursor: pointer;
  height: 40px;
  width: 150px;
  border-radius: 25px;
  left: 30%;
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 19px;
}

#submit_btn {
  color: var(--couleur_sumbit_btn);
}

.urgent_task {
  position: relative;
  color: red !important;
  text-decoration: underline;
  -webkit-text-decoration-color: red;
          text-decoration-color: red;
  text-underline-offset: 3px;
  text-decoration-thickness: 2px;
  text-transform: uppercase;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}
.modal_content {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  line-height: 1.4;
  background: var(--couleur_primaire);
  padding: 14px 28px;
  border-radius: 3px;
  width: 24rem;
  min-width: 300px;
}

.close_modal {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  cursor: pointer;
  width: 32px;
  height: 32px;
  padding: 0;
  background-color: var(--couleur_primaire);
}

.line {
  display: block;
  width: 100%;
  height: 3px;
  background: var(--couleur_secondaire);
  position: absolute;
}

.l1 {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.l2 {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.toggle_container {
  position: absolute;
  top: 1rem;
  right: 45%;
}

.toggle {
  visibility: hidden;
}

.toggle + label {
  display: flex;
  align-items: center;
  font-size: 1.5em;
  cursor: pointer;
  color: var(--couleur_secondaire);
}

.label_text {
  text-decoration: underline;
  text-underline-offset: 4px;
}

.toggle + label::before {
  content: "";
  height: 1em;
  width: 2em;
  border-radius: 1em;
  background-color: var(--couleur_tertiaire);
  margin-right: 0.5em;
  transition: background-color 0.25s ease-in-out;
}

.toggle + label::after {
  content: "";
  height: 0.8em;
  width: 0.8em;
  border-radius: 1em;
  background-color: var(--couleur_primaire);
  position: absolute;
  left: 0.2em;
  transition: background-color 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
  transition: background-color 0.25s ease-in-out, transform 0.25s ease-in-out;
  transition: background-color 0.25s ease-in-out, transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}

.toggle:checked + label::after {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.pagination {
  position: absolute;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  border: none;
  top: 77%;
  right: 49%;
}

.page_button {
  background-color: var(--couleur_pager_button);
  color: var(--couleur_secondaire);
  cursor: pointer;
  font-size: 20px;
}

.active {
  font-weight: 800;
  text-decoration: underline;
  text-underline-offset: 3px;
  text-decoration-thickness: 2px;
}

@media (min-width: 320px) and (max-width: 530px) {
  .header_container {
    width: 15rem !important;
    padding: 35px !important;
  }
  .titre_h1 {
    font-size: 20px;
  }
  #liste_element {
    font-size: 14px;
  }

  .label_text {
    font-size: 15px !important;
  }

  .toggle_container {
    right: 37% !important;
  }
}

.header_container {
  background-color: var(--couleur_tertiaire);
  padding: 46px;
  width: 23rem;
  height: 38em;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}

.titre_h1 {
  color: var(--couleur_titre_h1);
  position: relative;
  text-align: center;
  margin: -16px;
  border-bottom: 2px solid var(--couleur_secondaire);
}

:root {
  --couleur_primaire: #d9d5c9;
  --couleur_secondaire: #2c6545;
  --couleur_tertiaire: white;
  --couleur_titre_h1: black;
  --couleur_sumbit_btn: #d9d5c9;
  --couleur_pager_button: white;
}

[data-theme="dark"] {
  --couleur_primaire: #424242;
  --couleur_secondaire: #2c6545;
  --couleur_tertiaire: #1e1e1e;
  --couleur_titre_h1: #2c6545;
  --couleur_sumbit_btn: white;
  --couleur_pager_button: #1e1e1e;
}

/* Styles pour le bouton */
.btn_container {
  position: absolute;
  bottom: 4rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.btn {
  background-color: var(--couleur_secondaire);
  color: var(--couleur_tertiaire);
  border: none;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  font-size: 24px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
}

/* Style pour le symbole + */
.btn::before {
  content: "+";
  font-size: 50px;
}

* {
  text-decoration: none;
  font-family: "Nunito", "arial";
}

body {
  margin: 0;
  box-sizing: border-box;
  background-color: var(--couleur_primaire);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  transition: background-color 0.25s ease-in-out;
}

.dark_mode {
  background-color: var(--couleur_primaire_dark);
}

